import { Col, Row, Spin } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import BlogCardTwo from '../UiComponents/Cards/BlogCardTwo';
import GreetingBar from '../UiComponents/GreetingBar';
const AllBlogs = () => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);
  useEffect(() => {
    setspin(true);
    axios
      .get('https://ginnovation-server.herokuapp.com/api/blog')
      .then((resp) => {
        setspin(false);
        setdata(resp.data);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);
  return (
    <div className='page all-blog-page'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <GreetingBar />
      <section className='all-blogs'>
        <h3>New Blogs</h3>
        <div className='blog-container'>
          {data && (
            <Row gutter={8}>
              {data
                .filter((value) => value.status == 'pending')
                .map((value, index) => {
                  return (
                    <Col md={8} key={index}>
                      <BlogCardTwo value={value} type='new' />
                    </Col>
                  );
                })}
            </Row>
          )}
        </div>
        <h3 className='mt-5'>All Blogs</h3>
        <div className='blog-container'>
          {data && (
            <Row gutter={8}>
              {data
                .filter((value) => value.status == 'approved')
                .map((value, index) => {
                  return (
                    <Col md={8} key={index}>
                      <BlogCardTwo value={value} />
                    </Col>
                  );
                })}
            </Row>
          )}
        </div>
      </section>
    </div>
  );
};
export default AllBlogs;
