import React, { useEffect, useRef, useState } from 'react';
import GreetingBar from '../UiComponents/GreetingBar';
import { RetweetOutlined, CopyOutlined, SaveOutlined } from '@ant-design/icons';
import axios from 'axios';
import { message, Spin } from 'antd';
const ManageCode = () => {
  const [code, setcode] = useState('askd87asd');
  const [spin, setspin] = useState(false);
  const [refreshState, setrefreshState] = useState(1);
  const textAreaRef = useRef(null);
  useEffect(() => {
    setspin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/admin/token`)
      .then((resp) => {
        setspin(false);
        message.success('Fetched Code');
        setcode(resp.data[0].accesstoken);
      })
      .catch((err) => {
        setspin(false);
        message.error('Error getting code');
      });
  }, [refreshState]);
  const codes = [
    'askd87asd',
    'asdf737',
    'faj376r',
    'asdf83yas',
    'asj3r7yas',
    'fasdjhf7',
  ];
  const generateRandomCode = () => {
    const randomNumber = Math.floor(Math.random() * 6);
    setcode(codes[randomNumber]);
  };
  const saveCode = () => {
    setspin(true);
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/admin/token/ginnovation`,
        {
          accesstoken: code,
        }
      )
      .then((resp) => {
        setspin(false);
        if (resp.data.status == 'success') {
          message.success('Code Updated');
          setrefreshState(refreshState + 1);
        } else {
          message.warning('something went wrong');
        }
      })
      .catch((err) => {
        setspin(false);
        message.error('Code Update Failed');
      });
  };
  const copyToClipboard = (e) => {
    textAreaRef.current.select();
    document.execCommand('copy');
    message.success('copied');
  };
  return (
    <div className='page manage-code'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <GreetingBar />

      <div className='details'>
        <div className='stats'>
          <h5>Current Code</h5>
          <p>Last Updated: 25th Feb 12:40pm</p>
        </div>
        <div className='main'>
          <form>
            <textarea ref={textAreaRef} value={code} readOnly rows='1' />
            <h6 onClick={copyToClipboard}>
              Click to copy code <CopyOutlined className='icon' />
            </h6>
          </form>
        </div>
        <div className='btn-container'>
          <button onClick={generateRandomCode}>
            Generate New Code <RetweetOutlined className='icon' />
          </button>
          <button onClick={saveCode} className='active'>
            Save <SaveOutlined className='icon' />
          </button>
        </div>
      </div>
    </div>
  );
};

export default ManageCode;
