import React, { useEffect, useState } from 'react';
import { Col } from 'antd';
//antd imports
import { Progress } from 'antd';
import { StarFilled } from '@ant-design/icons';
//---------------------------------------------------------------
const SkillCard = (props) => {
  const [progress, setprogress] = useState(0);
  useEffect(() => {
    const ratings = props.stars;
    if (ratings === 1) {
      setprogress(20);
    } else if (ratings === 2) {
      setprogress(40);
    } else if (ratings === 3) {
      setprogress(60);
    } else if (ratings === 4) {
      setprogress(80);
    } else if (ratings === 5) {
      setprogress(100);
    }
  }, [props.stars]);
  const starRatings = <StarFilled className='icon' />;
  return (
    <Col md={12}>
      <div className='skill-card'>
        <div className='content'>
          <div className='header'>
            <h5>{props.skillName}</h5>
            <div className='icon-container'>
              {props.stars === 5 ? (
                <>
                  {starRatings}
                  {starRatings}
                  {starRatings}
                  {starRatings}
                  {starRatings}
                </>
              ) : (
                ''
              )}
              {props.stars === 4 ? (
                <>
                  {starRatings}
                  {starRatings}
                  {starRatings}
                  {starRatings}
                </>
              ) : (
                ''
              )}
              {props.stars === 3 ? (
                <>
                  {starRatings}
                  {starRatings}
                  {starRatings}
                </>
              ) : (
                ''
              )}
              {props.stars === 2 ? (
                <>
                  {starRatings}
                  {starRatings}
                </>
              ) : (
                ''
              )}
              {props.stars === 1 ? <>{starRatings}</> : ''}
            </div>
          </div>
          <p>{props.description}</p>
        </div>
        <Progress
          strokeColor={{
            from: '#108ee9',
            to: '#87d068',
          }}
          percent={progress}
          status='active'
        />
      </div>
    </Col>
  );
};
export default SkillCard;
