import React from 'react';
import { PlusCircleOutlined, PlusOutlined } from '@ant-design/icons';
// import FeaturedVideoSwiper from '../../UiComponents/HomePage/FeaturedVideoSwiper';
import ProjectCard from '../../UiComponents/Profile/ProjectCard';
import { Col, Row } from 'antd';

const ProfileProject = (props) => {
  const projects = props.data;
  const cards = projects.map((value, index) => {
    return (
      <Col md={8} key={index}>
        <ProjectCard value={value} editProject={props.editProject} editable />
      </Col>
    );
  });
  return (
    <section className='projects'>
      <div className='container'>
        {projects && projects.length !== 0 ? (
          <>
            <h4 className='section-head'>Projects by {props.username}</h4>
            {projects.length > 3 ? (
              {
                /* <FeaturedVideoSwiper
                data={projects}
                editProject={props.editProject}
              /> */
              }
            ) : (
              <Row gutter={12}>{cards}</Row>
            )}
            {props.editable && (
              <>
                <div className='edit-btn type2'>
                  <PlusOutlined onClick={props.addProject} />
                </div>
              </>
            )}
          </>
        ) : (
          <div className='fresh-section' onClick={props.addProject}>
            <header>
              <PlusCircleOutlined className='fresh-icon' />
              <p>Add Your Projects</p>
              <p>
                Remember, The more projects you will do, the more you will learn
              </p>
            </header>
          </div>
        )}
      </div>
    </section>
  );
};

export default ProfileProject;
