import React from 'react';
import img from '../../assets/sad.svg';
const MobilePage = () => {
  return (
    <div className='error-page mobile-page'>
      <div className='content'>
        <div>
          <img src={img} alt='not for mobile' className='img-fluid' />
          <h4 className='message'>
            Oops! You can't access this page through mobile devices
          </h4>
        </div>
      </div>
    </div>
  );
};

export default MobilePage;
