import React, { useEffect, useState } from 'react';
const GreetingBar = () => {
  const [clock, updateClock] = useState(new Date().toLocaleTimeString());
  const [greeting, setgreeting] = useState('Hey There!');

  let today = new Date();
  let dd = today.getDate();

  let mm = today.getMonth() + 1;
  const yyyy = today.getFullYear();
  if (dd < 10) {
    dd = `0${dd}`;
  }

  if (mm < 10) {
    mm = `0${mm}`;
  }
  today = `${mm}-${dd}-${yyyy}`;

  const refresh = () => {
    updateClock(new Date().toLocaleTimeString());
  };

  setInterval(refresh, 1000);
  const currentHour = new Date().getHours();
  useEffect(() => {
    if (currentHour >= 5 && currentHour < 12) {
      setgreeting('Good Morning Sir!');
    } else if (currentHour >= 12 && currentHour < 17) {
      setgreeting('Good Afternoon Sir!');
    } else if (currentHour >= 17 && currentHour < 24) {
      setgreeting('Good Evening Sir!');
    } else if (currentHour >= 0 && currentHour < 5) {
      setgreeting('You should sleep!');
    }
  }, [currentHour]);
  return (
    <header className='greeting-bar'>
      <h4 className='greeting'>{greeting}</h4>
      <h4 className='date-time'>
        {today} | {clock}
      </h4>
      <a href='https://ginnovation.club/' target='_blank' rel='noopener'>
        <button>Visit Website</button>
      </a>
    </header>
  );
};

export default GreetingBar;
