import { Form, Input, Button, Col, Empty, message, Row, Spin } from 'antd';
import React, { useEffect, useState } from 'react';
import { CaretRightOutlined } from '@ant-design/icons';
import GreetingBar from '../UiComponents/GreetingBar';
import newUsers from '../../assets/newUsers.svg';
import newBlogs from '../../assets/newBlogs.svg';
import newProjects from '../../assets/newProjects.svg';
import newVisitors from '../../assets/newVisitors.svg';
import axios from 'axios';
import AllUsers from '../UiComponents/AllUsers';
import { Link, useHistory } from 'react-router-dom';
const Home = () => {
  const [quoteData, setQuoteData] = useState();
  const [spin, setspin] = useState();
  const [data, setdata] = useState();
  let history = useHistory();

  function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    if (width < 600) {
      history.push('/mobile');
    }
    return {
      width,
      height,
    };
  }
  useEffect(() => {
    getWindowDimensions(); //window dimension to chk if mobile
    const randomNumber = Math.floor(Math.random() * 1640); //random number for quote api
    // quote api call
    axios
      .get('https://type.fit/api/quotes')
      .then((resp) => {
        setQuoteData(resp.data[randomNumber]);
      })
      .catch((err) => {
        // console.log(err);
      });
    setspin(true);
    // home api call
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/admin/home`)
      .then((resp) => {
        setspin(false);
        setdata(resp.data);
      })
      .catch((err) => {
        setspin(false);
      });
  }, []);

  return (
    <div className='page home'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <GreetingBar />
      <section className='dashboard'>
        <h5>Dashboard</h5>
        <Row gutter={12}>
          <Col md={6}>
            <Link to='/users/all'>
              <div className='review-card new-user'>
                <div className='figure'>
                  <img src={newUsers} alt='new users' />
                </div>
                <div className='content'>
                  <p>New Users</p>
                  <h2>10+</h2>
                </div>
              </div>
            </Link>
          </Col>
          <Col md={6}>
            <Link to='/projects/all'>
              <div className='review-card new-projects'>
                <div className='figure'>
                  <img src={newProjects} alt='new Projects' />
                </div>
                <div className='content'>
                  <p>New Projects</p>
                  <h2>27+</h2>
                </div>
              </div>
            </Link>
          </Col>
          <Col md={6}>
            <Link to='/blogs/all'>
              <div className='review-card new-blogs'>
                <div className='figure'>
                  <img src={newBlogs} alt='new Blogs' />
                </div>
                <div className='content'>
                  <p>New Blogs</p>
                  <h2>20+</h2>
                </div>
              </div>
            </Link>
          </Col>
          <Col md={6}>
            <div className='review-card new-visitors'>
              <div className='figure'>
                <img src={newVisitors} alt='new users' />
              </div>
              <div className='content'>
                <p>New Visitors</p>
                <h2>50+</h2>
              </div>
            </div>
          </Col>
        </Row>
      </section>
      <section className='info'>
        <Row gutter={10}>
          <Col md={10}>
            <div className='mentors-info'>
              <h5>Ginnovation Mentors</h5>
              <table>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Branch</th>
                    <th>More</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Preetom Palo</td>
                    <td>ECE</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>Preetom Palo</td>
                    <td>ECE</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>Preetom Palo</td>
                    <td>ECE</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>Preetom Palo</td>
                    <td>ECE</td>
                    <td>-</td>
                  </tr>
                  <tr>
                    <td>Preetom Palo</td>
                    <td>ECE</td>
                    <td>-</td>
                  </tr>
                </tbody>
              </table>
              <button>
                More <CaretRightOutlined />
              </button>
            </div>
          </Col>
          <Col md={7}>
            <div className='user-stats'>
              <h5>User Stats</h5>
              <Row gutter={10}>
                <Col md={12}>
                  <div className='stats-card total-students'>
                    <div>
                      <h2>{data && data.totalStudents}+</h2>
                      <p>Total Students</p>
                    </div>
                  </div>
                </Col>
                <Col md={12}>
                  <div className='stats-card total-mentors'>
                    <div>
                      <h2>{data && data.totalMentors}+</h2>
                      <p>Total Mentors</p>
                    </div>
                  </div>
                </Col>
                <Col md={12}>
                  <div className='stats-card total-teachers'>
                    <div>
                      <h2>{data && data.totalTeachers}+</h2>
                      <p>Total Teachers</p>
                    </div>
                  </div>
                </Col>
                <Col md={12}>
                  <div className='stats-card total-projects'>
                    <div>
                      <h2>10+</h2>
                      <p>Total Projects</p>
                    </div>
                  </div>
                </Col>
              </Row>
            </div>
          </Col>
          <Col md={7}>
            <div className='quote-panel'>
              <h5>Motivation Corner</h5>
              <div className='panel'>
                {quoteData ? (
                  <div className='card'>
                    <h4>{quoteData.text}</h4>
                    <em>- {quoteData.author}</em>
                  </div>
                ) : (
                  <Empty />
                )}
              </div>
            </div>
          </Col>
        </Row>
      </section>
      <section>
        <h5>All Users List</h5>
        <AllUsers />
      </section>
    </div>
  );
};

export default Home;
