import React from 'react';
import { CalendarOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import ReactHtmlParser from 'react-html-parser';

const BlogCard = ({ value, refresh }) => {
  const cardTitle = value.blogTitle.slice(0, 30) + '...';
  const fallbackImg =
    'https://www.elegantthemes.com/blog/wp-content/uploads/2019/03/png-jpg-gif-featured-image.jpg';
  const cover = {
    backgroundImage: `url(${
      value.coverpic.substring(0, 4) == 'data' ||
      value.coverpic.substring(0, 4) == 'http'
        ? value.coverpic !== undefined
          ? value.coverpic
          : fallbackImg
        : fallbackImg
    })`,
  };
  const dateTime = value.createdAt;
  const link = `blog/${value._id}`;
  let rawdate = dateTime.slice(0, 10);
  let date = rawdate.split('-').reverse().join('-');
  return (
    <div className='blog-card'>
      <div className='card-top' style={cover}>
        <span className='about'>About</span>
        <span className='date'>27th MAR</span>
      </div>
      <div className='card-content'>
        <h5 className='title'>{cardTitle}</h5>
        <p className='sub-heading'>{value.subTitle}</p>
        <div className='c-body'>
          <p className='content'>{ReactHtmlParser(value.blogContent)}</p>
          <Link to={link}>
            <p className='link'>...Read More</p>
          </Link>
        </div>
        <div className='card-bottom'>
          <span>
            <CalendarOutlined className='icon' />
            {date}
          </span>
          <div>
            <Link to={link}>
              <button>Full Article</button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlogCard;
