import Home from './Components/PageComponents/HomePage';
import './Sass/main.scss';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import ErrorPage from './Components/PageComponents/ErrorPage';
import { Layout } from 'antd';
import Sidebar from './Components/UiComponents/Sider/Sidebar';
import AllBlogs from './Components/PageComponents/AllBlogs';
import NewBlogs from './Components/PageComponents/NewBlogs';
import NewProjects from './Components/PageComponents/NewProjects';
import AllProjcts from './Components/PageComponents/AllProjects';
import AllUsersData from './Components/PageComponents/AllUsersData';
import ViewProfile from './Components/PageComponents/ViewProfile';
import ViewProject from './Components/PageComponents/ViewProject';
import ViewBlog from './Components/PageComponents/ViewBlog';
import ManageAlumni from './Components/PageComponents/ManageAlumni';
import MangeStudents from './Components/PageComponents/MangeStudents';
import ManageCode from './Components/PageComponents/ManageCode';
import MobilePage from './Components/PageComponents/MobilePage';

function App() {
  return (
    <div className='App'>
      <Router>
        <Layout style={{ minHeight: '100vh' }}>
          <Sidebar />
          <Layout className='site-layout'>
            <Switch>
              <Route exact path='/' component={Home} />
              <Route exact path='/blogs/all' component={AllBlogs} />
              <Route exact path='/blogs/new' component={NewBlogs} />
              <Route exact path='/projects/all' component={AllProjcts} />
              <Route exact path='/projects/new' component={NewProjects} />
              <Route exact path='/users/all' component={AllUsersData} />
              <Route exact path='/manage/alumni' component={ManageAlumni} />
              <Route exact path='/manage/students' component={MangeStudents} />
              <Route exact path='/manage/code' component={ManageCode} />
              <Route exact path='/mobile' component={MobilePage} />
              {/* dynamic routings */}
              <Route exact path='/profile/:userId' component={ViewProfile} />
              <Route path='/project/:projectId' component={ViewProject} />
              <Route path='/blog/:blogId' component={ViewBlog} />
              <Route component={ErrorPage} />
            </Switch>
          </Layout>
        </Layout>
      </Router>
    </div>
  );
}

export default App;
