import React from 'react';
import fb from '../../../assets/Icons/facebook.png';
import li from '../../../assets/Icons/linkedin.png';
import gh from '../../../assets/Icons/github.png';
import ig from '../../../assets/Icons/instagram.png';
import {
  DeleteOutlined,
  EditOutlined,
  CheckCircleTwoTone,
  AliwangwangFilled,
} from '@ant-design/icons';

const AlumniCard = (props) => {
  const profileImg = {
    backgroundImage: `url(${props.imgSrc})`,
  };
  return (
    <div className='people-card alumni-card'>
      <div className='cover'>
        <button className='action-btn delete' onClick={props.handleDelete}>
          <DeleteOutlined />
        </button>
        <button className='action-btn edit' onClick={props.handleEdit}>
          <EditOutlined />
        </button>
        <div className='placed'>
          <CheckCircleTwoTone twoToneColor='#52eb06' className='icon' />
          Placed
        </div>
      </div>
      <div className='people-card-body'>
        <div className='profile-picture' style={profileImg}></div>
        <h5>{props.name}</h5>
        <p className='headline'>{props.headline}</p>
        <hr />
        <p className='branch'>{props.branch}</p>
        <hr />
        <div className='message'>
          <button onClick={props.handleMessage}>
            <AliwangwangFilled className='icon' />
            Alumni Message
          </button>
        </div>
        <div className='sm-container'>
          <a
            href={props.liLink && props.liLink}
            target='_blank'
            rel='noreferrer'
            className={
              props.liLink !== null && props.liLink !== '' ? '' : 'disabled'
            }
          >
            <img src={li} alt='alumni linkedin handle' />
          </a>
          <a
            href={props.igLink && props.igLink}
            target='_blank'
            rel='noreferrer'
            className={
              props.igLink !== null && props.igLink !== '' ? '' : 'disabled'
            }
          >
            <img src={ig} alt='alumni instagram handle' />
          </a>
          <a
            href={props.fbLink && props.fbLink}
            target='_blank'
            rel='noreferrer'
            className={
              props.fbLink !== null && props.fbLink !== '' ? '' : 'disabled'
            }
          >
            <img src={fb} alt='alumni facebook handle' />
          </a>
          <a
            href={props.ghLink && props.ghLink}
            target='_blank'
            rel='noreferrer'
            className={
              props.ghLink !== null && props.ghLink !== '' ? '' : 'disabled'
            }
          >
            <img src={gh} alt='alumni github handle' />
          </a>
        </div>
      </div>
    </div>
  );
};

export default AlumniCard;
