import React, { useEffect, useState } from 'react';
import { Layout, Menu, message, Form, Input, Button } from 'antd';

import {
  DesktopOutlined,
  PieChartOutlined,
  LogoutOutlined,
  LoginOutlined,
  StarOutlined,
  BarcodeOutlined,
  LockOutlined,
  UserOutlined,
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

const { Sider } = Layout;
const { SubMenu } = Menu;
const Sidebar = () => {
  const [collapsed, setcollapsed] = useState(false);
  const [loggedIn, setloggedIn] = useState(false);

  const Logout = () => {
    localStorage.removeItem('token');
    message.success('Successfully LoggedOut');
    window.location.reload();
  };
  const token = localStorage.getItem('token');

  useEffect(() => {
    if (token == null) {
      setloggedIn(false);
      // Logout();
    } else {
      setloggedIn(true);
    }
  }, [token]);
  const onFinish = (values) => {
    if (values.email == 'admin@gmail.com' && values.password == 'admin') {
      setloggedIn(true);
      message.success('Login Successful');
      localStorage.setItem('token', 12345);
    } else {
      message.error('Login Failed');
    }
    // setSpin(true);
    // axios
    //   .post(`https://ginnovation-server.herokuapp.com/api/login`, {
    //     email: values.email,
    //     password: values.password,
    //   })
    //   .then((resp) => {
    //     setSpin(false);
    //     console.log(resp);
    //     if (resp.data.status === 'success') {
    //       message.success('LogIn Successful');
    //       localStorage.setItem('token', resp.data.token);
    //       localStorage.setItem('uid', resp.data.uid);
    //       dispatch(authFunction(true));
    //       setPopConfirm(true);
    //     } else {
    //       message.warning('Oops! Something went wrong');
    //     }
    //   })
    //   .catch((err) => {
    //     setSpin(false);

    //     err.response
    //       ? message.error(
    //           err.response.data.status === 'invalid credentials'
    //             ? 'Invalid Credentials! Check your email and password'
    //             : 'Something went wrong!'
    //         )
    //       : message.error("Ouch! There's a error");
    //   });
  };

  return (
    <div className='sider-container'>
      <Sider
        collapsible
        collapsed={collapsed}
        onCollapse={() => {
          setcollapsed(!collapsed);
        }}
        breakpoint='sm'
        collapsedWidth='80'
      >
        <div className='head'>
          <div className='avatar'></div>
          {collapsed ? <p>Ginno</p> : <h6>Ginnovation</h6>}
          {collapsed ? <h6>ADMIN</h6> : <h5>ADMIN</h5>}
        </div>
        <Menu theme='light' defaultSelectedKeys={['1']} mode='inline'>
          <Menu.Item key='1' icon={<PieChartOutlined />}>
            Home
            <Link to='/' />
          </Menu.Item>
          <Menu.Item key='2' icon={<DesktopOutlined />}>
            Manage Students
            <Link to='/manage/students' />
          </Menu.Item>
          <Menu.Item key='3' icon={<StarOutlined />}>
            Manage Alumni
            <Link to='/manage/alumni' />
          </Menu.Item>
          <Menu.Item key='4' icon={<BarcodeOutlined />}>
            Manage Code
            <Link to='/manage/code' />
          </Menu.Item>
          {/* <Menu.Item key='5' icon={<LoginOutlined />}>
            LogIn
            <Link to='/login' />
          </Menu.Item> */}
          <Menu.Item key='6' icon={<LogoutOutlined />} onClick={Logout}>
            LogOut
          </Menu.Item>
          {/* <SubMenu key='sub1' icon={<UserOutlined />} title='User'>
            <Menu.Item key='3'>Tom</Menu.Item>
            <Menu.Item key='4'>Bill</Menu.Item>
            <Menu.Item key='5'>Alex</Menu.Item>
          </SubMenu>
          <SubMenu key='sub2' icon={<TeamOutlined />} title='Team'>
            <Menu.Item key='6'>Team 1</Menu.Item>
            <Menu.Item key='8'>Team 2</Menu.Item>
          </SubMenu>
          <Menu.Item key='9' icon={<FileOutlined />}>
            Files
          </Menu.Item> */}
        </Menu>
      </Sider>
      {loggedIn ? (
        ''
      ) : (
        <div className='login-panel'>
          <div>
            <div className='avatar'></div>
            <h1>Admin Login</h1>
            <p>Enter a valid email and password to access the admin panel</p>
            <Form
              name='login'
              className='login-form'
              initialValues={{ remember: true }}
              onFinish={onFinish}
            >
              <Form.Item
                name='email'
                rules={[
                  {
                    required: true,
                    message: 'Please input your Email!',
                  },
                ]}
              >
                <Input
                  prefix={<UserOutlined className='site-form-item-icon' />}
                  type='email'
                  placeholder='Email'
                />
              </Form.Item>
              <Form.Item
                name='password'
                rules={[
                  {
                    required: true,
                    message: 'Please input your Password!',
                  },
                ]}
              >
                <Input.Password
                  prefix={<LockOutlined className='site-form-item-icon' />}
                  type='password'
                  placeholder='Password'
                />
              </Form.Item>
              <Form.Item>
                <center>
                  <Button
                    type='primary'
                    htmlType='submit'
                    className='login-form-button ant-space-align-center'
                  >
                    Submit
                  </Button>
                </center>
              </Form.Item>
            </Form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Sidebar;
