import { Col, message, Row, Spin, Modal } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import AlumniCard from '../UiComponents/Cards/AlumniCard';
import GreetingBar from '../UiComponents/GreetingBar';
import { UserAddOutlined } from '@ant-design/icons';
import AddAlumni from '../UiComponents/Modals/AddAlumni';
import EditAlumni from '../UiComponents/Modals/EditAlumni';
const ManageAlumni = () => {
  const [spin, setspin] = useState(false);
  const [refreshId, setrefreshId] = useState(1);
  const [data, setdata] = useState();
  const [addAlumni, setaddAlumni] = useState(false);
  const [editAlumni, seteditAlumni] = useState(false);
  const [editValue, seteditValue] = useState();
  const [alumnibatch, setalumniBatch] = useState();
  const [alumniId, setalumniId] = useState();
  const [messageModal, setmessageModal] = useState(false);
  const [alumniMessage, setalumniMessage] = useState();
  let alumniBatches = [];

  useEffect(() => {
    setspin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/alumni`)
      .then((resp) => {
        console.log(resp.data);
        setspin(false);
        setdata(resp.data);
        resp.data &&
          resp.data.map((value, index) => {
            alumniBatches.push(value.batch);
          });
        const uniqueBatches = [...new Set(alumniBatches)];
        setalumniBatch(uniqueBatches);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, [refreshId]);

  const deleteAlumni = (id) => {
    setspin(true);
    axios
      .delete(`https://ginnovation-server.herokuapp.com/api/alumni/data/${id}`)
      .then((resp) => {
        setspin(false);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          setrefreshId(refreshId + 1);
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setspin(false);
        message.error('An error occured!');
      });
  };

  return (
    <div className='page manage-alumni'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <GreetingBar />
      <div>
        {alumnibatch &&
          alumnibatch.map((batchValue, index) => {
            return (
              <div key={index}>
                <h4 className='year'>
                  Alumni Batch of {batchValue - 4}-{batchValue}
                </h4>
                <Row gutter={10}>
                  {data &&
                    data
                      .filter((value) => value.batch == batchValue)
                      .map((value, index) => {
                        return (
                          <Col md={6} key={index} className='mb-2'>
                            <AlumniCard
                              imgSrc={value.profilePicture}
                              name={value.fullname}
                              headline={value.designation}
                              branch={value.branch}
                              about={value.about}
                              liLink={value.links[0].linkedin}
                              ghLink={value.links[0].github}
                              igLink={value.links[0].instagram}
                              fbLink={value.links[0].facebook}
                              handleEdit={() => {
                                setalumniId(value._id);
                                seteditValue(value);
                                seteditAlumni(true);
                              }}
                              handleDelete={() => {
                                deleteAlumni(value._id);
                              }}
                              handleMessage={() => {
                                setalumniMessage('hi');
                                setmessageModal(true);
                              }}
                            />
                          </Col>
                        );
                      })}
                  <Col md={6}>
                    <div
                      className='add-card'
                      onClick={() => setaddAlumni(true)}
                    >
                      <UserAddOutlined className='icon' />
                    </div>
                  </Col>
                </Row>
              </div>
            );
          })}
      </div>
      <button className='add-alumni' onClick={() => setaddAlumni(true)}>
        <UserAddOutlined className='icons' />
      </button>
      {addAlumni && (
        <AddAlumni
          handleClose={(dataChanged) => {
            setaddAlumni(false);
            if (dataChanged == true) {
              setrefreshId(refreshId + 1);
            }
          }}
        />
      )}
      {editAlumni && (
        <EditAlumni
          handleClose={(dataChanged) => {
            seteditAlumni(false);
            if (dataChanged == true) {
              setrefreshId(refreshId + 1);
            }
          }}
          data={editValue}
          id={alumniId}
        />
      )}
      <Modal
        title='Alumni Message'
        visible={messageModal}
        onCancel={() => setmessageModal(false)}
        footer={null}
        className='message-modal'
      >
        <p>{alumniMessage}</p>
      </Modal>
    </div>
  );
};

export default ManageAlumni;
