import { message, Spin, Tooltip } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import NoImg from '../../assets/noimg.png';
import ParseTime from '../../Functions/ParseTime';
import {
  CalendarOutlined,
  CheckOutlined,
  CloseOutlined,
  DeleteOutlined,
  ExclamationCircleFilled,
  CheckSquareFilled,
} from '@ant-design/icons';
import blogIcon from '../../assets/Icons/blog.png';
import ReactHtmlParser from 'react-html-parser';
const ViewBlog = ({ match }) => {
  const [data, setdata] = useState();
  const [userdata, setuserdata] = useState();
  const [spin, setSpin] = useState();

  const {
    params: { blogId },
  } = match;

  useEffect(() => {
    setSpin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/viewblog/${blogId}`)
      .then((resp) => {
        console.log(resp.data);
        setSpin(false);
        setdata(resp.data);
        axios
          .get(
            `https://ginnovation-server.herokuapp.com/api/profile/users/${resp.data.creatorsID}`
          )
          .then((resp) => {
            setuserdata(resp.data);
          })
          .catch((err) => {
            message.error('error fetching the user data');
          });
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('error');
      });
  }, [blogId]);

  return (
    <div className='container blog-details'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      {data && (
        <>
          <div className='head'>
            <h3>
              <img src={blogIcon} alt='ginnovation blogs' /> Ginnovation Blogs
            </h3>
          </div>
          <img
            src={data.coverpic !== null ? data.coverpic : NoImg}
            alt='blog-cover'
            onError={(e) => {
              e.target.onerror = null;
              e.target.src =
                'https://expresswriters.com/wp-content/uploads/2017/06/header-1210x423.jpg';
            }}
          />
          <header>
            <div
              className='profile-img'
              style={{
                backgroundImage: userdata && `url(${userdata.profilePicture})`,
              }}
            ></div>
            <h4>{userdata && userdata.fullname}</h4>
            {userdata && (
              <p>
                {userdata.education[0].semester +
                  ' Semester' +
                  ', ' +
                  userdata.education[0].branch}
              </p>
            )}
            <p className='flex-center'>
              <CalendarOutlined className='icon' />
              Created on {ParseTime(data.createdAt)}
            </p>
          </header>
          <hr />
          <div className='blog-content container'>
            <div className='heading'>
              <h1>{data.blogTitle}</h1>
              <h5>{data.subTitle}</h5>
            </div>
            <div className='content'>{ReactHtmlParser(data.blogContent)}</div>
          </div>
          {data.status == 'pending' ? (
            <div className='info'>
              <p className='approve-status not-approved'>
                Not approved yet <ExclamationCircleFilled className='icon' />
              </p>
              <div className='action'>
                <h5>Hey Admin! What's your thought on this blog</h5>
                <div className='btn-container'>
                  <Tooltip title='Approve Blog'>
                    <button>
                      <CheckOutlined className='icons' />
                    </button>
                  </Tooltip>
                  <Tooltip title='Delete Blog'>
                    <button>
                      <CloseOutlined className='icons' />
                    </button>
                  </Tooltip>
                </div>
              </div>
            </div>
          ) : (
            <div className='info'>
              <p className='approve-status'>
                This blog has been approved{' '}
                <CheckSquareFilled className='icon' />
              </p>
              <div className='action danger'>
                <h5>If anything goes wrong you can delete this Blog</h5>
                <div className='btn-container'>
                  <Tooltip title='Delete Blog'>
                    <button>
                      <DeleteOutlined className='icons' />
                    </button>
                  </Tooltip>
                </div>
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default ViewBlog;
