import React, { useEffect, useState } from 'react';
//antd imports
import { message, Spin } from 'antd';
//components import
// import MoreAchievementModal from '../UiComponents/ProfilePage/Modals/MoreAchievementModal';
//axios
import axios from 'axios';
//animation
import ProfileTop from '../SectionComponents/Profile/ProfileTop';
import BioAchievement from '../SectionComponents/Profile/BioAchievement';
import Skills from '../SectionComponents/Profile/Skills';
import ProfileBlog from '../SectionComponents/Profile/ProfileBlog';
import ProfileProject from '../SectionComponents/Profile/ProfileProject';
//---------------------------------------------------------------------
const ViewProfile = ({ match }) => {
  const [spin, setSpin] = useState(false);
  const [userData, setUserData] = useState();
  const [userName, setUserName] = useState();
  const [blogData, setBlogData] = useState();
  const [projectData, setProjectData] = useState();
  const [moreAchievements, setMoreAchievements] = useState(false);
  //

  const {
    params: { userId },
  } = match;

  //get profile data
  useEffect(() => {
    setSpin(true);
    axios
      .get(
        `https://ginnovation-server.herokuapp.com/api/profile/users/${userId}`
      )
      .then((resp) => {
        setSpin(false);
        setUserData(resp.data);
        setUserName(resp.data.fullname);
        //
        setSpin(true);
        axios
          .all([
            axios.get(
              `https://ginnovation-server.herokuapp.com/api/blog/${resp.data.email}`
            ),
            axios.get(
              `https://ginnovation-server.herokuapp.com/api/project/${resp.data.email}`
            ),
          ])
          .then(
            axios.spread((resp1, resp2) => {
              setSpin(false);
              setBlogData(resp1.data);
              setProjectData(resp2.data);
            })
          )
          .catch((err) => {
            setSpin(false);
            message.error("There's a error fetching blog and project data");
          });
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('error');
      });
  }, [userId]);

  const userMailAddress = userData ? `mailto:${userData.email}` : '';

  return (
    <div>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='profile-page'>
        {userData && projectData && (
          <>
            <ProfileTop
              data={userData}
              projects={projectData.projects}
              mail={userMailAddress}
            />
            <BioAchievement
              data={userData}
              moreAchievements={() => {
                setMoreAchievements(true);
              }}
            />
            <Skills data={userData} username={userName} />
            {projectData && (
              <ProfileProject username={userName} data={projectData} />
            )}
            {blogData && <ProfileBlog username={userName} data={blogData} />}
            {/* ----------------------------------------- */}
            {/* {moreAchievements && (
              <MoreAchievementModal
                data={userData.achievements}
                handleClose={() => {
                  setMoreAchievements(false);
                }}
              />
            )} */}
          </>
        )}
      </div>
    </div>
  );
};
export default ViewProfile;
