import React from 'react';
import { Row } from 'antd';
//icons import
import { EditOutlined, PlusCircleOutlined } from '@ant-design/icons';
//componets import
import SkillCard from '../../UiComponents/Profile/SkillCard';
//---------------------------------------------------------------------
const Skills = (props) => {
  const data = props.data;
  return (
    <section className='skills'>
      <div className='container'>
        {data.skills.length !== 0 ? (
          <>
            <h4 className='section-head'>{props.username}'s Skills</h4>
            <Row gutter={12} className='skill-container'>
              {data.skills.map((value, index) => {
                return (
                  <SkillCard
                    skillName={value.title}
                    stars={value.rating}
                    description={value.description}
                    key={index}
                  />
                );
              })}
            </Row>
            {props.editable && (
              <div className='edit-btn type2' onClick={props.openModal}>
                <EditOutlined />
              </div>
            )}
          </>
        ) : (
          <div className='fresh-section' onClick={props.openModal}>
            <header>
              <PlusCircleOutlined className='fresh-icon' />
              <p>Add Your Top Skills</p>
              <p>
                Remember, Reading is the gateway skill that makes all other
                learning possibles
              </p>
            </header>
          </div>
        )}
      </div>
    </section>
  );
};
export default Skills;
