import { Col, Row } from 'antd';
import React from 'react';
//icons import
import { PlusOutlined, PlusCircleOutlined } from '@ant-design/icons';
import BlogCard from '../../UiComponents/Profile/BlogCard';
const ProfileBlog = (props) => {
  const blogs = props.data;
  return (
    <section className='blogs'>
      <div className='container'>
        {blogs && blogs.length !== 0 ? (
          <>
            <h4 className='section-head'>Blogs by {props.username}</h4>
            <Row gutter={12} className='blog-container'>
              {blogs.map((value, index) => {
                return (
                  <Col md={8} key={index}>
                    <BlogCard value={value} refresh={props.refresh} />
                  </Col>
                );
              })}
            </Row>
            {props.editable && (
              <>
                <div className='edit-btn type2' onClick={props.addBlog}>
                  <PlusOutlined />
                </div>
              </>
            )}
          </>
        ) : (
          <div className='fresh-section' onClick={props.addBlog}>
            <header>
              <PlusCircleOutlined className='fresh-icon' />
              <p>Add Your Blogs</p>
              <p>Share your amazing thoughts by writing down a fresh Blog</p>
            </header>
          </div>
        )}
      </div>
    </section>
  );
};

export default ProfileBlog;
