import React, { useState } from 'react';
//antd imports
import { Form, Input, Button, Row, Col, Spin, message, Checkbox } from 'antd';
import { CloseOutlined } from '@ant-design/icons';
import axios from 'axios';
//
const AddAlumni = (props) => {
  const [dataChanged, setDataChanged] = useState(false);
  const [spin, setSpin] = useState(false);
  const [form] = Form.useForm();
  console.log(props.data);
  const onFinish = (values) => {
    setDataChanged(true);
    const updatedData = {
      fullname: values.fullname,
      profilePicture: values.profilePicture,
      designation: values.designation,
      branch: values.branch,
      batch: values.batch,
      links: [
        {
          linkedin: values.linkedin,
          github: values.github,
          instagram: values.instagram,
          facebook: values.facebook,
          twitter: values.twitter,
        },
      ],
    };
    setSpin(true);
    axios
      .post(
        `https://ginnovation-server.herokuapp.com/api/alumni/data`,
        updatedData
      )
      .then((resp) => {
        setSpin(false);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          props.handleClose(true);
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        message.error('An error occured!');
      });
  };
  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal'>
        <h4>
          Add a New Alumni
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>
        <Form form={form} name='addAlumni' onFinish={onFinish}>
          <Row gutter={8}>
            <Col md={12}>
              <Form.Item
                name='fullname'
                rules={[
                  {
                    required: true,
                    message: 'Please input the name!',
                  },
                ]}
              >
                <Input placeholder='Enter Alumni Name' />
              </Form.Item>
            </Col>
            <Col md={12}>
              <Form.Item
                name='designation'
                rules={[
                  {
                    required: true,
                    message: 'Please input the designation!',
                  },
                ]}
              >
                <Input placeholder='Enter Designation' />
              </Form.Item>
            </Col>

            <Col md={8}>
              <Form.Item
                name='profilePicture'
                rules={[
                  {
                    required: true,
                    message: 'Please input the image link!',
                  },
                ]}
              >
                <Input placeholder='Enter Profile Image Link' />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='branch'
                rules={[
                  {
                    required: true,
                    message: 'Please input the branch!',
                  },
                ]}
              >
                <Input placeholder='Enter Alumni Branch' />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='batch'
                rules={[
                  {
                    required: true,
                    message: 'Please input the year!',
                  },
                ]}
              >
                <Input placeholder='Graduation Year' />
              </Form.Item>
            </Col>
            <Col md={5}>
              <Form.Item name='linkedin'>
                <Input placeholder='Linkedin Link' />
              </Form.Item>
            </Col>
            <Col md={5}>
              <Form.Item name='github'>
                <Input placeholder='Github Link' />
              </Form.Item>
            </Col>
            <Col md={5}>
              <Form.Item name='instagram'>
                <Input placeholder='Instagram Link' />
              </Form.Item>
            </Col>
            <Col md={5}>
              <Form.Item name='facebook'>
                <Input placeholder='Facebook Link' />
              </Form.Item>
            </Col>
            <Col md={4}>
              <Form.Item name='twitter'>
                <Input placeholder='TT Link' />
              </Form.Item>
            </Col>
            <Col md={6}>
              <Form.Item name='placed'>
                <Checkbox>Placed</Checkbox>
              </Form.Item>
            </Col>
          </Row>

          <div className='form-btn-row'>
            <div className='register-btn-container btn-container mt-3'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Button type='primary' htmlType='submit'>
                Upload
              </Button>
            </div>
          </div>
        </Form>
      </div>
    </div>
  );
};

export default AddAlumni;
