import React from 'react';
import { Col, Row } from 'antd';
//icons import
import {
  InfoCircleTwoTone,
  DownOutlined,
  PlusCircleOutlined,
} from '@ant-design/icons';
//assets imports
import trophy from '../../../assets/Icons/trophy.png';
const BioAchievement = (props) => {
  const data = props.data;
  return (
    <section className='about-section'>
      <div className='container'>
        <Row gutter={8}>
          <Col md={16}>
            {data.biodata ? (
              <div className='bio'>
                <h4>Professional Bio</h4>
                <p>{data.biodata}</p>
              </div>
            ) : (
              <div className='fresh-section' onClick={props.openBio}>
                <header>
                  <PlusCircleOutlined className='fresh-icon' />
                  <p>Add Your Bio</p>
                  <p>
                    Write something intresting about you to let people know
                    about you
                  </p>
                </header>
              </div>
            )}
          </Col>
          <Col md={8} className='achievement-col'>
            {data.achievements.length !== 0 ? (
              <div className='achievements'>
                <>
                  <h4>
                    <img src={trophy} alt='student achievements ginnovation' />
                    Top Achievements
                  </h4>
                  <div>
                    {data.achievements.slice(0, 3).map((value, index) => {
                      return (
                        <div className='achievement' key={index}>
                          <span>{value.title}</span>
                          <a href={value.link} target='_blank' rel='noreferrer'>
                            <InfoCircleTwoTone className='icon' />
                          </a>
                        </div>
                      );
                    })}
                  </div>
                  {data.achievements.length > 3 && (
                    <div className='more-btn' onClick={props.moreAchievements}>
                      <DownOutlined className='icon' />
                    </div>
                  )}
                </>
              </div>
            ) : (
              <div className='fresh-section' onClick={props.openAchievement}>
                <header>
                  <PlusCircleOutlined className='fresh-icon' />
                  <p>Add Your Achievements</p>
                  <p>Let people know about your glories</p>
                </header>
              </div>
            )}
          </Col>
        </Row>
      </div>
    </section>
  );
};
export default BioAchievement;
