import React, { useEffect, useState } from 'react';
import { Table, Tag, Space } from 'antd';
import axios from 'axios';
import { SearchOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
const AllUsers = () => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);

  useEffect(() => {
    setspin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/profile/users`)
      .then((resp) => {
        setspin(false);
        setdata(resp.data);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);

  const columns = [
    {
      title: '',
      dataIndex: 'dp',
      key: 'dp',
      width: 40,
      render: (url) => (
        <div
          style={{
            height: '30px',
            width: '30px',
            backgroundImage: `url('${url}')`,
            backgroundPosition: 'center',
            backgroundSize: 'cover',
          }}
        ></div>
      ),
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Role',
      dataIndex: 'role',
      key: 'role',
      render: (userType) => {
        let color = '';
        if (userType == 'Student') {
          color = 'geekblue';
        } else if (userType == 'Mentor') {
          color = 'green';
        } else {
          color = 'volcano';
        }
        return <Tag color={color}>{userType}</Tag>;
      },
    },
    {
      title: 'Address',
      dataIndex: 'address',
      key: 'address',
    },
    {
      title: 'Projects',
      dataIndex: 'projects',
      key: 'projects',
    },
    {
      title: 'Blogs',
      dataIndex: 'blogs',
      key: 'blogs',
    },
    {
      title: 'Action',
      key: 'action',
      render: (link) => {
        return (
          <Space size='middle'>
            <Link to={link.action}>View Profile</Link>
          </Space>
        );
      },
    },
  ];

  let tableData =
    data &&
    data.map((value, index) => {
      const link = `/profile/${value._id}`;
      return {
        key: index,
        dp: value.profilePicture,
        name: value.fullname,
        role: value.role,
        projects: 15,
        blogs: 25,
        tags: ['daasdfas'],
        address: 'jk',
        action: link,
      };
    });
  return (
    <div>
      <Table columns={columns} dataSource={tableData} />
    </div>
  );
};

export default AllUsers;
